@import url("https://fonts.googleapis.com/css2?family=Azeret+Mono:wght@100;400;800&family=Nova+Mono&family=Xanh+Mono&display=swap");
.menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100vh;
  width: 100%;
  background: #6ebad4;
}

.menu #menuContainer {
  position: absolute;
  z-index: 1;
  height: 92vh;
  width: 96%;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.3);
  -webkit-box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
          box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  overflow: hidden;
}

.menu #menuContainer h1 {
  position: absolute;
  z-index: 2;
  top: 20%;
  left: 18%;
  font-size: 170px;
  color: aliceblue;
  text-shadow: 2px 3px 2px black, -2px -3px 2px black;
  font-family: "Nova Mono", monospace;
  text-transform: uppercase;
}

.menu #menuContainer h2 {
  position: absolute;
  z-index: 2;
  top: 30%;
  left: 30%;
  font-size: xx-large;
  font-family: "Azeret Mono", monospace;
  letter-spacing: 2px;
}
